
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import SelectTab from "components/SelectTab/selectTab";
export default {
data() {
return {
noData: false,
select1: [{key:'0',val:'全部'},{key:'1',val:'处方'},{key:'2',val:'病历'}],
TabDefault1:{key:'0',val:'全部'},
TabDefault2:{key:'3',val:'近三月'},
select2: [{key:'1200',val:'全部'},{key:'3',val:'近三月'},{key:'12',val:'近一年'},{key:'36',val:'近三年'}],
type: 0,  // 1处方 2病历 0全部
start: "",
num: 3,
pageIndex: 1,
pageSize: 15,
list: [],
};
},
mounted() {
this.getCurrentDate();
this.getOutpatientRecordList(true);
},
methods: {

selectChange1(obj) {
this.type = obj.key;
this.getOutpatientRecordList(true);
},

selectChange2(obj) {
this.num = obj.key;
this.getOutpatientRecordList(true);
},

getCurrentDate() {
var date = new Date();
var seperator = "";
var year = date.getFullYear();
var month = date.getMonth() + 1;
if (month >= 1 && month <= 9) {
month = "0" + month;
}
this.start = year + seperator + month;
},

getOutpatientRecordList(upOrDown) {
if (upOrDown) {
this.pageIndex = 1;
this.list = [];
}else{
this.pageIndex++;
}
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "outpatientDepartmentLog",
"MSH.2": "list",
"grdaid": this.$root.loginInfo.grdaid,
"CHZBM": this.$root.loginInfo.CHZBM,
"type": this.type,
"start": this.start,
"num": this.num,
"pageIndex": this.pageIndex,
"pageSize": this.pageSize,
'token':this.$root.loginInfo.token,
}).then(res => {
this.$vux.loading.hide();
if(typeof res != 'undefined' && typeof res.rows != 'undefined') {
if (res.rows.length > 0) {
this.noData = false;
this.list = this.list.concat(res.rows);
} else {
if (res.pageNumber > 1) {
this.noData = false;
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.noData = true;
}
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
})
},

addClass(ISCF) {
if (ISCF == "1") {
return "pres";
} else if (ISCF == "0") {
return "medre";
}
},

gotoDetail(ISCF, ID, NY, YLJGID, JZLSH) {
if (ISCF == "1") {
this.$router.push({name: "OutpatientPrescription", params: {ID: ID, NY: NY}});
} else if (ISCF == "0") {
this.$router.push({name: "medicalRecords", params: {NY: NY, YLJGID: YLJGID, JZLSH: JZLSH}});
}
}
},
components: {
XHeader,
SelectTab
}
};



